import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { axiosPost } from "../services/axiosService";

function Debug() {
  const navigate = useNavigate();
  const [data, setData] = useState<string>("");

  async function debugBot() {
    if (!data) return;
    const response = await axiosPost("/ai/debug-bot", { data });
    navigate(`/builder/${response.data.data.url}`);
  }

  return (
    <div>
      <textarea style={{ height: "700px", width: "100%" }} onChange={(e) => setData(e.target.value)}>
        {data}
      </textarea>
      <button onClick={debugBot}>submit</button>
    </div>
  );
}

export default Debug;
